import axios from 'axios';

// Konfigurace axios
const API_BASE_URL = 'https://inc-internal.onrender.com/api';

const api = axios.create({
  baseURL: API_BASE_URL,
  timeout: 60000, // zveno na 60 sekund
  headers: {
    'Content-Type': 'application/json',
  },
});

class ChatService {
  // Vytvoit nov thread
  static async createThread(userInfo) {
    try {
      const response = await api.post('/chat/thread', userInfo || {});
      return response.data;
    } catch (error) {
      console.error('Error creating thread:', error);
      throw new Error('Nepodailo se vytvoit nov thread');
    }
  }

  // Odeslat zprvu
  static async sendMessage(message, threadId) {
    try {
      const response = await api.post('/chat/message', {
        message,
        threadId
      });
      return response.data;
    } catch (error) {
      console.error('Error sending message:', error);
      
      if (error.response) {
        // Server vrtil chybu
        const errorMessage = error.response.data?.message || 'Nepodailo se odeslat zprvu';
        throw new Error(errorMessage);
      } else if (error.request) {
        // Sov chyba
        throw new Error('Nepodailo se pipojit k serveru');
      } else {
        // Jin chyba
        throw new Error('Nastala neoekvan chyba');
      }
    }
  }

  // Zskat historii zprv
  static async getThreadMessages(threadId) {
    try {
      const response = await api.get(`/chat/thread/${threadId}`);
      return response.data;
    } catch (error) {
      console.error('Error getting thread messages:', error);
      throw new Error('Nepodailo se nast historii zprv');
    }
  }

  // Kontrola pipojen k serveru
  static async checkHealth() {
    try {
      const response = await api.get('/health');
      return response.data;
    } catch (error) {
      console.error('Health check failed:', error);
      throw new Error('Server nen dostupn');
    }
  }
}

export default ChatService; 